/* Full-page layout adjustments */
html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    font-family: 'Poppins', sans-serif;
    background: linear-gradient(to right, #8EC5FC, #E0C3FC);
    overflow-x: hidden;
    min-height: 100vh;
    background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
    background-attachment: fixed;
    padding: 20px;
}

/* Dashboard Container */
.dashboard-container {
    display: flex;
    height: 100vh;
    width: 100vw;
}

/* Sidebar */
.sidebar {
    width: 200px;
    background: linear-gradient(to bottom, #6a11cb, #2575fc);
    padding: 20px;
    box-shadow: 4px 0 15px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    height: 100vh;
    position: fixed;
    top: 0;
    left: 0;
    border-radius: 0 20px 20px 0;
    color: white;
    overflow-y: auto;
}

.sidebar h2 {
    text-align: center;
    color: #FFD700;
    font-size: 22px;
    font-weight: 600;
    margin-bottom: 20px;
}

/* Sidebar Navigation */
.sidebar nav ul {
    list-style: none;
    padding: 0;
}

.sidebar nav ul li {
    padding: 12px 15px;
    margin: 8px 0;
    border-radius: 10px;
    transition: all 0.3s ease-in-out;
    cursor: pointer;
    display: flex;
    align-items: center;
    gap: 12px;
}

.sidebar nav ul li:hover {
    background: rgba(255, 255, 255, 0.1);
    transform: translateX(5px);
}

.menu-item {
    text-decoration: none;
    font-weight: 500;
    display: flex;
    align-items: center;
    gap: 10px;
    color: white;
    width: 100%;
    padding: 8px 12px;
    border-radius: 8px;
    transition: all 0.3s ease;
    border: none;
    background: linear-gradient(45deg, #4CAF50, #45a049);
    cursor: pointer;
}

.menu-item:hover {
    background: linear-gradient(45deg, #45a049, #4CAF50);
    transform: translateX(5px);
}

.menu-item.active {
    background: linear-gradient(45deg, #45a049, #4CAF50);
    color: #FFD700;
    font-weight: 600;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
}

/* Remove the specific button styles since we want them to be the same */
.menu-item.home,
.menu-item.orders {
    background: linear-gradient(45deg, #4CAF50, #45a049);
}

.menu-item.home:hover,
.menu-item.orders:hover {
    background: linear-gradient(45deg, #45a049, #4CAF50);
}

/* Main Content */
.main-content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
}

/* Top Navigation */
.top-nav {
    width: calc(100% - 200px);
    padding: 5px 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
    background: rgba(255, 255, 255, 0.3);
    backdrop-filter: blur(10px);
    border-radius: 0 10px 10px 0;
    position: sticky;
    top: 0;
    z-index: 1000;
    height: 50px;
    margin-left: 283px;
    margin-bottom: 20px;
}

.top-nav h1 {
    margin: 0;
    font-size: 24px;
    color: #2c3e50;
}

/* Search Bar */
.search-container {
    flex: 1;
    display: flex;
    justify-content: center;
}

.search-container input {
    width: 400px;
    padding: 12px 15px;
    border: 2px solid #ddd;
    border-radius: 10px;
    font-size: 16px;
    transition: 0.3s;
}

.search-container input:focus {
    border-color: #6a0dad;
    outline: none;
    box-shadow: 0 0 10px rgba(106, 13, 173, 0.3);
}

.logout-btn {
    background: #ff4d4d;
    color: white;
    padding: 8px 20px;
    border-radius: 12px;
    font-size: 16px;
    cursor: pointer;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    gap: 8px;
    border: none;
    margin-left: auto; /* Push the button to the far right */
    margin-right: 30px; /* Add spacing from the right edge */
    white-space: nowrap; /* Prevent text from wrapping */
}
    


.logout-btn:hover {
    background: #e60000;
    transform: scale(1.05);
}

/* Dashboard Welcome */
.dashboard-welcome {
    text-align: center;
    margin-top: 30px;
}

.dashboard-welcome h1 {
    font-size: 38px;
    font-weight: bold;
    color: #6a0dad;
    transition: 0.3s;
}

.dashboard-welcome h1:hover {
    transform: scale(1.02);
}

.dashboard-welcome p {
    font-size: 18px;
    color: #444;
    margin-top: 10px;
}

/* Grid Layout */
.grid-container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
    width: 100%;
    padding: 20px;
}

/* Grid Item Styling */
.grid-item {
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 10px;
    text-align: center;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    width: 90%;
    margin: 0 auto;
    background: white;
}

.grid-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

/* Image Styling */
.item-image {
    width: 100%;
    height: 150px;
    object-fit: contain;
    border-radius: 10px;
}

/* Item Details */
.item-title {
    font-size: 18px;
    font-weight: bold;
    margin-top: 10px;
}

.item-description {
    color: #666;
    font-size: 14px;
    margin: 10px 0;
}

.item-price {
    color: #6a11cb;
    font-weight: bold;
    font-size: 16px;
}

/* Borrow Button */
.borrow-button {
    background-color: #007bff;
    color: white;
    padding: 8px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.borrow-button:hover {
    background-color: #0056b3;
}

/* Quantity Container */
.quantity-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 10px 0;
    gap: 10px;
}

.quantity-button {
    background-color: #6a11cb;
    color: white;
    border: none;
    padding: 5px 10px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease;
}

.quantity-button:hover {
    background-color: #5a0db3;
}

.quantity-value {
    font-size: 16px;
    font-weight: bold;
    color: #333;
}

/* Order Button */
.order-button {
    background-color: #fd5628;
    color: white;
    padding: 8px 15px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    margin-top: 15px;
    transition: background-color 0.3s ease;
}

.order-button:hover {
    background-color: #e04e24;
}

/* Order Info */
.order-info {
    background: #f8f9fa;
    padding: 10px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    user-select: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
}

.order-info-row {
    display: flex;
    justify-content: space-between;
    margin: 5px 0;
    font-size: 0.9em;
}

/* Order Status Badge */
.order-status-badge {
    position: absolute;
    top: 15px;
    right: 15px;
    background: #6a11cb;
    color: white;
    padding: 5px 12px;
    border-radius: 20px;
    font-size: 12px;
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.5px;
}

/* Menu styles */
.menu-list {
    list-style: none;
    padding: 0;
    margin: 20px 0;
}

.menu-item {
    display: flex;
    align-items: center;
    padding: 12px 15px;
    color: white;
    text-decoration: none;
    border-radius: 4px;
    margin-bottom: 10px;
    transition: all 0.3s ease;
    background: rgba(255, 255, 255, 0.1);
}

.menu-item:hover {
    background: rgba(255, 255, 255, 0.2);
    transform: translateX(5px);
}

.menu-item.active {
    background: rgba(255, 255, 255, 0.3);
    font-weight: 600;
}

/* Dashboard title */
.dashboard-title {
    font-size: 24px;
    margin-bottom: 30px;
    color: white;
    text-align: center;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
}

/* Loading and Error States */
.loading-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    min-height: 400px;
    width: 100%;
}

.loading-spinner {
    width: 50px;
    height: 50px;
    border: 5px solid #f3f3f3;
    border-top: 5px solid #6a11cb;
    border-radius: 50%;
    animation: spin 1s linear infinite;
    margin-bottom: 20px;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}

.error-container {
    text-align: center;
    padding: 40px;
    background: white;
    border-radius: 10px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.error-message {
    color: #dc3545;
    margin-bottom: 20px;
}

.retry-button {
    background-color: #6a11cb;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s ease;
}

.retry-button:hover {
    background-color: #5a0db3;
}

/* No Orders State */
.no-orders {
    text-align: center;
    padding: 40px;
    background: white;
    border-radius: 10px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
}

.empty-state-icon {
    font-size: 48px;
    margin-bottom: 20px;
}

.no-orders h2 {
    color: #6a11cb;
    margin-bottom: 10px;
}

.no-orders p {
    color: #666;
    margin-bottom: 20px;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
    .grid-container {
        grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    }

    .search-container input {
        width: 100%;
    }
}

