body {
    display: flex;
    justify-content: center;  /* Center horizontally */
    align-items: center;      /* Center vertically */
    height: 100vh;            /* Full viewport height */
    background-color: #f4f4f4; /* Light background */
    margin: 0;
}

.container {
    background: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    text-align: center; /* Align content in center */
    border: 2px solid #007bff; /* Added blue border */
    position: relative; /* Allows positioning of child elements */
}

/* User/Admin Option Styling */
.user-admin-option {
    position: absolute; /* Position it inside the container */
    top: 10px; /* Place it at the top */
    left: 50%; /* Center horizontally */
    transform: translateX(-50%); /* Adjust for centering */
    font-size: 18px; /* Increase font size */
    font-weight: bold; /* Make it bold */
    color: #007bff; /* Blue color */
}

/* Input Fields */
input {
    display: block;
    width: 250px;
    padding: 10px;
    margin: 10px auto; /* Center input fields */
    border: 1px solid #ccc;
    border-radius: 5px;
}

/* Year Select Option */
select.year-select {
    display: block;
    width: 270px; /* Increase width */
    padding: 12px; /* Increase padding */
    margin: 10px auto; /* Center the select field */
    border: 1px solid #ccc;
    border-radius: 5px;
    font-size: 16px; /* Increase font size */
}

/* Button Styling */
button {
    background-color: #007bff;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
}

button:hover {
    background-color: #0056b3;
}
